
































import Vue from 'vue';
import { ModelEntity } from '@/live2d/ModelEntity';

export default Vue.extend({
    name: "ModelList",
    props: {
        value: Number,
        show: Boolean,
    },
    data: () => ({
        models: [] as ModelEntity[],
    }),
    created() {
        this.models = this.$live2dApp.models;
    },
    methods: {
        select(id: number) {
            this.$emit('input', id);
        },
    },
});
